
















import {Component, Vue} from 'vue-property-decorator';

import {AppState} from '@/store/modules/App';

@Component
export default class Hamburger extends Vue {

    private toggleClick() {
        this.$emit('toggleClick');
    }

    get sidebar() {
        return AppState.sidebar;
    }

    get isActive() {
        return this.sidebar.opened;
    }
}
