







import {Component, Prop, Vue} from 'vue-property-decorator';

import {isExternal} from '@/utils/ValidateUtils';

@Component
export default class AppLink extends Vue {
    @Prop(String)
    private to!: string;

    private linkProps(url: string) {
        if (isExternal(url)) {
            return {
                is: 'a',
                href: url,
                target: '_blank',
                rel: 'noopener',
            };
        }
        return {
            is: 'router-link',
            to: url,
        };
    }
}
